import { HttpClient } from '@angular/common/http';
import { GetGamingEventApiRest } from '../../operations/gaming/get-gaming-event';
import { EventsTypeApiRest } from '../events-type';

export class GamingEventsApiRest extends EventsTypeApiRest {

  constructor(
    http: HttpClient, urlBase: string, token: string, basePath: string
  ) {
    super(http, urlBase, token, basePath);
    this.get = new GetGamingEventApiRest(
      http,
      urlBase + basePath,
      token
    );
  }

  public get: GetGamingEventApiRest;
}
