import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

class GameTypes {
  public readonly Boardgame: string = 'boardgame';
  public readonly BoardgameExpansion: string = 'boardgameexpansion';
  public readonly error: string = 'error';

  public readonly languaje: {
    [type: string]: string;
  } = {
    [this.Boardgame]: 'Juegos',
    [this.BoardgameExpansion]: 'Expansiones',
    [this.error]: 'Errores en la busqueda',
  };

  public getTypes(): string[] {
    return [
      this.Boardgame,
      this.BoardgameExpansion,
      this.error
    ];
  }
}

class ApiPoints {
  public gameTypes: GameTypes = new GameTypes();
  private readonly url = 'https://www.boardgamegeek.com/';
  private readonly apiPath = 'xmlapi2/';
  private readonly searchPath = 'search/?type=' + this.gameTypes.Boardgame + '&query='; // + string
  private readonly boardgameByIdPath = 'thing?type=' + this.gameTypes.Boardgame + ',' + this.gameTypes.BoardgameExpansion + '&id='; // + id
  /**
   * Url to search a game in the bgg, you need to add the name of the game at the end.
   */
  public readonly searchByName = this.url + this.apiPath + this.searchPath;
  /**
   * Url to get a game in the bgg, you need to add the id of the game at the end.
   */
  public readonly getBoardgameById = this.url + this.apiPath + this.boardgameByIdPath;
}

@Injectable({
  providedIn: 'root'
})
export class BggApiRest {

  constructor(private http: HttpClient) { }

  private api: ApiPoints = new ApiPoints();
  public gameTypes = this.api.gameTypes;

  public requestBoardGame(id: number): Observable<any> {
    return this.http.get(this.api.getBoardgameById + id, { responseType: 'text' });
  }

  public requestSearch(name: string): Observable<any> {
    return this.http.get(this.api.searchByName + name, { responseType: 'text' });
  }
}
