import { Injectable } from '@angular/core';
import { ImagesAndTypeFix } from '../../../../shared/models/bggGame';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

class ApiPoints {
  public readonly ImagesAndType = 'typeAndImages';
  public readonly ImagesAndTypeError = 'errorInTypeAndImages';
}

@Injectable({
  providedIn: 'root'
})
export class BggFireBaseApiRest {
  public readonly urlBase = 'https://bgg-games-info.firebaseio.com/';
  private readonly token = '?auth=' + '5Cz6RklemHPl26Maj4eNrgE9jny4g5ITsi1Fb1V3';

  constructor(private http: HttpClient) { }

  private api: ApiPoints = new ApiPoints();

  private get(url: string) {
    return this.http.get(
      this.urlBase + url + this.token,
      { responseType: 'json' }
    );
  }

  private patch(obj: any, url: string) {
    return this.http.patch(
      this.urlBase + url + this.token,
      obj,
      { headers: { 'content-type': 'application/json' }}
    );
  }

  private generateIndexId(id: number): string {
    if (id >= 100000000) {
      return '' + Math.floor(id / 100000000) + this.mapRouteIndexId(id % 100000000);
    } else {
      this.mapRouteIndexId(id);
      if(id >= 1000000) {
        return '' + this.mapRouteIndexId(id);
      } else {
        if (id >= 10000) {
          return '00/' + this.mapRouteIndexId(id);
        } else if (id >= 100) {
          return '00/00/' + this.mapRouteIndexId(id);
        } else  {
          return '00/00/00/' + this.mapRouteIndexId(id);
        }
      }
    }
  }

  private mapRouteIndexId(id: number): string {
    if (id < 100) {
      if (id < 10) {
        return '0' + id;
      }
      return '' + id;
    } else {
      let result: string;
      if (id % 100 < 10) {
        result = '0' + id % 100;
      } else {
        result = '' + id % 100;
      }
      return '' + this.mapRouteIndexId(Math.floor(id / 100)) + '/' + result;
    }
  }

  public addImagesAndType(id: number, gameInfo: ImagesAndTypeFix) {
    return this.patch(gameInfo, this.api.ImagesAndType + '/' + this.generateIndexId(id) + '.json');
  }

  public getImagesAndType(id: number) {
    return this.get(this.api.ImagesAndType + '/' + this.generateIndexId(id) + '.json');
  }

  public addImagesAndTypeErrors(error: string, id: number) {
    // const insert = { [id]: -1 };
    return this.patch('-1', this.api.ImagesAndTypeError + '/' + error + '/' + this.generateIndexId(id) + '.json');
  }

  public getImagesAndTypeErrors(error: string, id: number) {
    return this.get(this.api.ImagesAndTypeError + '/' + error + '/' + this.generateIndexId(id) + '.json');
  }
}