import { Component, OnInit } from '@angular/core';
import { GameEventsDetailComponent } from './game-event/game-event-detail/game-events-detail.component';
import { MatDialog } from '@angular/material/dialog';
import { FirebaseBOService } from '../../firebase/valenciaybuenrollo/shared/firebase-bo.service';
import { LocalDate } from '../../shared/localdate/model/localdate';
import { DateUtils } from '../../shared/localdate/date-utils';
import { take } from 'rxjs/operators';
import { Dialogs } from '../../shared/dialogs/dialogs';
import { GamingEventsDay, GamingEvent } from '../../shared/models/events/event/gaming/gamingEvent';

@Component({
  selector: 'app-game-events',
  templateUrl: './game-events.component.html',
  styleUrls: ['./game-events.component.scss']
})
export class GameEventsComponent implements OnInit {

  gamingEventsDayList: GamingEventsDay[];
  selectedEvent: GamingEvent;
  index = '0';

  constructor(
    private firebase: FirebaseBOService,
    private details: MatDialog,
  ) { }

  ngOnInit() {
    // this.firebase.getGameEvents().pipe(take(1)).subscribe({
    this.firebase.events.gaming.get.all().pipe(take(1)).subscribe({
      next: (x: GamingEventsDay[]) => {
        this.gamingEventsDayList = x;
      },
      error: (err) => {
        Dialogs.showInfo('No se ha encontrado ninguna quedada en la base de datos.');
        this.gamingEventsDayList = [];
      }
    });
  }

  length(object: object): number {
    if (object) {
      return Object.keys(object).length;
    } else {
      return 0;
    }
  }

  showDate(date: LocalDate): string {
    return new DateUtils().showDate(date);
  }

  openDetails(event: GamingEvent): void {
    this.selectedEvent = event;
    // eventAux = Object.assign({}, this.selectedEvent)
    const detailsRef = this.details.open(GameEventsDetailComponent, {
      width: '90%',
      data: Object.assign({}, this.selectedEvent)
      // data: this.selectedEvent
    });

    detailsRef.afterClosed().subscribe().unsubscribe();
  }
}
