import { HttpClient } from '@angular/common/http';
import { GamingEventsApiRest } from './types/gaming/gaming-event';
import { EventTypes } from '../../../../shared/models/types/events/events-types';
import { ParticipantTypes } from '../../../../shared/models/types/events/participants/participants-types';

export class EventsApiRest {

  constructor(
    http: HttpClient,
    urlBase: string,
    token: string,
    basePath: string,
  ) {
    this.gaming = new GamingEventsApiRest(
      http,
      urlBase,
      token,
      basePath + this.eventsTypes.gaming + '/'
    );
  }

  public eventsTypes = new EventTypes();
  public participantTypes = new ParticipantTypes();

  public gaming: GamingEventsApiRest;

  public setToken(token: string) {
    this.gaming.get.setToken(token);
    this.gaming.post.setToken(token);
    this.gaming.patch.setToken(token);
    this.gaming.delete.setToken(token);
  }
}
