import { BggSearchGameItem } from '../models/bgg-objects';
import { BggGame, BggGamesclassifieds } from 'src/app/shared/models/bggGame';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class BggUtils {

  /**
   * Convert the Object BggSearchGameItem into BggGame.
   *
   * @param item the item to convert.
   */
  public convertToGameFromSearch(item: BggSearchGameItem): BggGame {
    const game: BggGame = {} as BggGame;
    game.id = item['@_id'];
    game.name = item.name['@_value'];
    game.nameType = item.name['@_type'];
    game.type = item['@_type'];
    if ( item.yearpublished ) {
      game.yearpublished = item.yearpublished['@_value'] || '';
    } else {
      game.yearpublished = '';
    }
    return game;
  }

  /**
   * Classifies games according to their type as BggApi.GameTypes.
   * Like 'Juegos', 'Expansiones' y 'Errores en la busqueda'.
   *
   * @param games The list with the games to classify.
   */
  public ClassifyGamesByType(games: BggGame[], types: string[], classifications: { [type: string]: string }): BggGamesclassifieds[] {
    const gamesbyType: BggGamesclassifieds[] = [] as BggGamesclassifieds[];
    if ( games.length ) {
      types.forEach(type => {
        const filteredGames: BggGame[] = games.filter(
          (game: BggGame) => {
            return game.type === type;
          }
        );
        if ( filteredGames.length ) {
          const classifiedGames: BggGamesclassifieds = {} as BggGamesclassifieds;
          classifiedGames.type = classifications[type];
          classifiedGames.games = filteredGames;
          gamesbyType.push(classifiedGames);
        }
      });
    }
    return gamesbyType;
  }
}
