import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { LocalDate } from '../../../../shared/localdate/model/localdate';
import { DateUtils } from '../../../../shared/localdate/date-utils';
import { STATE } from '../../../../app-state';
import { GamingEvent } from '../../../../shared/models/events/event/gaming/gamingEvent';

@Component({
  selector: 'app-game-events-thumbnail',
  templateUrl: './game-events-thumbnail.component.html',
  styleUrls: ['./game-events-thumbnail.component.scss']
})
export class GameEventsThumbnailComponent implements OnInit {
  @Input()
  idDay: string;
  @Input()
  event: GamingEvent;

  constructor(private router: Router) { }

  ngOnInit() { }

  length(object: object): number {
    if (object) {
      return Object.keys(object).length;
    } else {
      return 0;
    }
  }

  showDate(date: LocalDate): string {
    return new DateUtils().showDate(date);
  }

  openDetails(): void {
    STATE.setArgs({
      idDay: this.idDay,
      event: this.event
    });
    this.router.navigate(['juegos/' + this.idDay + '/' + this.event.id]);
  }
}
